.product {
    border: 1px solid #ddd;
    padding: 16px;
    text-align: center;
}

.product-image {
    width: 100%;
    height: auto;
}

.product h3 {
    margin: 0 0 10px;
}

.product p {
    margin: 0 0 10px;
}

.product button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
}

.product button:hover {
    background-color: #0056b3;
}
